.constructor {
  max-width: 600px;
  grid-area: constructor;
  box-sizing: border-box;
}
.ingredients {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.list {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}
.list_toppings {
  composes: list;
  max-height: calc(100vh - 284px - 252px);
  overflow-y: scroll;
  scrollbar-width: thin;
}
.list_toppings::-webkit-scrollbar {
  width: 8px;
}

.list_toppings::-webkit-scrollbar-track {
  background: #2f2f37;
}

.list_toppings::-webkit-scrollbar-thumb {
  background: #8585ad;
}

.order {
  display: grid;
  grid-template-columns: 1fr 215px;
  align-items: center;
}
.total__price {
  justify-self: end;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  gap: 8px;
}
.hint {
  text-align: center;
}
.onHover {
  border-radius: 10%;
  border: 1px solid #8585ad;
  box-sizing: content-box;
}
